import React, { Component } from "react";
import { Header, Label } from "semantic-ui-react";

const categoryStyle = {
  display: "flex",
  flexDirection: "column",
  justifyContent: "space-between",
  alignItems: "center",
  height: "100%",
};

const headerStyle = {
  width: "90%",
  height: "30%",
  marginTop: "35%",
  textAlign: "left",
  fontSize: "30px",
  // color: "#3080DF",
};

const labelStyle = {
  marginTop: "10px",
  padding: "10px",
  background: "white",
};

class Category extends Component {
  render() {
    const categoryNumber = this.props.category;
    const color = this.props.color;

    //  
    const mainCode = ["CATFOO", "CATELE", "CATCLO"];
    const mainText = ["", "", ""];
    const hotKeywords = [
      ["", "", "", "", ""],
      ["", "", "", "", ""],
      ["", "", "", "", "/"],
    ];

    const label = hotKeywords[categoryNumber].map((key) => (
      <Label
        as="a"
        href={`/list?search=${key}`}
        style={{ ...labelStyle, color: color, border: `solid ${color} 1px` }}
      >
        #{key}
      </Label>
    ));

    return (
      <div style={categoryStyle}>
        <Header style={{ ...headerStyle, color: color }}>
          {mainText[categoryNumber]}
          <br />
          <a
            style={{ fontSize: "15px", color: "black" }}
            href={`/list?category=${mainCode[categoryNumber]}`}
          >
            
          </a>
        </Header>
        <div
          style={{
            width: "90%",
            height: "50%",
            textAlign: "left",
          }}
        >
          <b style={{ fontSize: "18px" }}>Hot</b>
          <div
            style={{
              display: "flex",
              flexDirection: "column",
              alignItems: "flex-start",
            }}
          >
            {label}
          </div>
        </div>
      </div>
    );
  }
}

export default Category;
